.btnBox {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px
}

button {
    background-color: #4CAF50; /* Green */
        border: none;
        color: white;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        margin: 10px;
        cursor: pointer;
}

button:hover {
    color: gray;
}